.jMosaic-clear {
  clear: both;
}

.jMosaic-selector {
  clear: both;
  position: relative;
}

.jMosaic-selector > .jMosaic-item {
  display: block;
  float: left;
  position: relative;
  overflow: hidden;
  width: auto;
  max-height: 350px !important;
  object-fit: cover;
}

.jMosaic-selector > .jMosaic-item:hover,
.jMosaic-selector > .jMosaic-item:focus {}
